module.exports = {"data":[{"category":"General notice","title":"Scope of the General notice","hash":"scope-of-the-general-notice","intro":"<p><strong>Scope of the General notice</strong><br></p><p>Tieto is made up of different legal entities, details of which can be found here [<a href=\"https://www.tieto.com/addresses\" target=\"_blank\" rel=\"noopener noreferrer\">https://www.tieto.com/addresses</a>]. This General notice is issued on behalf of the Tieto Group so when we mention ”Tieto”, “we”, “us” or “our” in this notice, we are referring to the relevant company in the Tieto Group responsible for processing your data.</p><p>This Tieto General notice aims to give you information on how Tieto collects and processes your personal data and tells you about your privacy rights and how the law protects you. It is important that you read this General notice together with any other privacy notices we may provide on specific occasions when we are collecting or processing personal data about you so that you are fully aware of how and why we are using your data. These specific privacy notices can be found by clicking on the applicable heading provided above. This General notice supplements the other notices and is not intended to override them.</p><p>Our website is not intended for children and we do not knowingly collect data relating to children. This notice does not form any part of contract to provide services.</p><p><strong>Data Controller of your personal data</strong></p><p>The “data controller” means the entity that will make the decisions abour how your data is used and that is responsible for deciding how it holds personal information about you.</p><p>Since Tieto is made up of different legal entities, the entity who will be the controller for your data is dependent on the situation where your personal data is collected. The following entities will be Data Controllers in relation to you:</p><p> <ul> <li>Tieto Corporation (Tieto Oyj), and</li><li>Tieto affiliate that you are interacting with in the specific situation, for example the Tieto affiliate that is organizing the event you are participating to or the Tieto affiliate who is the employer in the recruitment process you taking part in.</li></ul></p>","content":""},{"category":"General notice","title":"Data protection principles","hash":"data-protection-principles","intro":"<p>Tieto group of companies (”Tieto”) is committed to protecting the privacy and security of your personal information, in accordance with the applicable privacy legislation.</p><p>This says that the personal information we hold about you must be:</p><p> <ol> <li>Used lawfully, fairly and in a transparent way.</li><li>Collected only for valid purposes that we have clearly explained to you and not used in any way that is incompatible with those purposes.</li><li>Relevant to the purposes we have told you about and limited only to those purposes.</li><li>Accurate and kept up to date.</li><li>Kept only as long as necessary for the purposes we have told you about.</li><li>Kept securely.</li></ol></p>","content":""},{"category":"General notice","title":"Personal data categories","hash":"personal-data-categories-1","intro":"<p> Personal data, or personal information, means any information about an individual from which that person can be identified. It does not include data where the identity has been removed (anonymous data). We collect, use, store and transfer different kinds of personal data about you depending on the situation. For more specific information on the processed data, please see the specific privacy notices provided on this website.</p>","content":"<p>We also collect, use and share Aggregated Data such as statistical or demographic data for any purpose. Aggregated Data may be derived from your personal data but is not considered personal data in law as this data does not directly or indirectly reveal your identity.</p><p>We do not generally collect any special categories of personal data about you (this includes details about your race or ethnicity, religious or philosophical beliefs, sex life, sexual orientation, political opinions, trade union membership, information about your health and genetic and biometric data). Nor do we collect any information about criminal convictions and offences. In the limited cases where we do process such data, we do this in accordance with the applicable legislation.</p>"},{"category":"General notice","title":"Collection of personal data","hash":"collection-of-personal-data-1","intro":"<p>We use different methods to collect data from and about you including through direct interactions, automated technologies or publicly available sources.</p><p>Where we need to collect personal data by law, or under the terms of a contract we have with you and you fail to provide that data when requested, we may not be able to perform the contract we have or are trying to enter into with you (for example, to provide you with goods or services). In this case, we may have to cancel a product or service you have with us but we will notify you if this is the case at the time.</p>","content":""},{"category":"General notice","title":"Purpose of processing","hash":"purpose-of-processing","intro":"<p>We will only use your personal information when the law allows us to. </p><p>We will use your personal information in the following circumstances:</p><p> <ul> <li>Where you have given consent to the processing of your personal information for one or more specific purposes.</li><li>Where the processing of your personal data is necessary for the performance of a contract to which you are a party or to take steps at your request before entering into such a contract.</li><li>Where we need to comply with a legal or regulatory obligation. This means processing your personal data where it is necessary for compliance with a legal or regulatory obligation that we are subject to.</li><li>Where it is necessary for our legitimate interests (or those of a third party) and your interests and fundamental rights do not override those interests. We make sure and we consider and balance any potential impact on you (both positive and negative) and your rights before we process your personal data for our legitimate interests. We do not use your personal data for activities where our interests are overridden by the impact on you (unless we have your consent or are otherwise required or permitted to by law). You can obtain further information about how we assess our legitimate interests against any potential impact on you in respect of specific activities by contacting us.</li><li>We may also use your personal information in the following situations, which are likely to be rare: <ul> <li>Where we need to protect your interests (or someone else’s interests).</li><li>Where it is needed in the public interest [or for official purposes].</li></ul> </li></ul></p>","content":"<p>We will only use your personal data for the purposes for which we collected it, unless we reasonably consider that we need to use it for another reason and that reason is compatible with the original purpose. If you wish to get an explanation as to how the processing for the new purpose is compatible with the original purpose, please contact us at privacy@tieto.com.</p><p>If we need to use your personal data for an unrelated purpose, we will notify you and we will explain the legal basis which allows us to do so. Please note that we may process your personal data without your knowledge or consent, in compliance with the above rules, where this is required or permitted by law.</p>"},{"category":"General notice","title":"Data sharing","hash":"data-sharing","intro":"<p><strong>Sharing within Tieto group</strong></p><p>We will share your personal information with other entities in our group as part of our regular reporting activities on company performance, in the context of a business reorganisation or group restructuring exercise and for system maintenance support and hosting of data.</p><p>Tieto group entities acting as data controllers towards you are Tieto Corporation and the Tieto affiliate as defined in Scope of the General Notice / Data Controller of your personal data of this General notice. Other Tieto group entities, if any, shall operate as data processors processing your personal data on behald of the data controllers.</p><p><strong>Third parties acting as data processors</strong></p><p>We transfer your data with third-party service providers (including contractors and designated agents). All our third-party service providers and other entities in the group are required to take appropriate security measures to protect your personal information and are not allowed to process your personal information for their own purposes.</p><p><strong>Third parties acting as data controllers</strong></p><p>We may disclose your personal information also with third parties acting as data controllers where required by law, where it is necessary to administer the relationship with you or where we have another legitimate interest in doing so. This can be for example when </p><p> <ul> <li>explicitly requested by you</li><li>there is a legitimate purpose for the disclosure, such as when we are facilitating a joint event or conference with a third party and have informed you that the data will be shared with such third parties</li><li>we are conducting a merger, acquisition or another corporate transaction</li><li>we need to comply with any legal and mandatory obligation, or to enforce and protect the rights, property, or safety of Tieto, our clients, or others.</li></ul></p>","content":""},{"category":"General notice","title":"Transferring information outside the EU/EEA","hash":"transferring-information-outside-the-eu-eea","intro":"<p>We share your personal data within the Tieto Group. This will involve transferring your data outside the European Economic Area (EEA). In addition, our external third parties are based outside the European Economic Area (EEA) so their processing of your personal data will involve a transfer of data outside the EEA.</p><p>Whenever we transfer your personal data out of the EEA, we ensure a similar degree of protection is afforded to it by ensuring at least one of the following safeguards is implemented:</p>","content":"<p> <ul> <li>We will only transfer your personal data to countries that have been deemed to provide an adequate level of protection for personal data by the European Commission. For further details, see <a href=\"https://ec.europa.eu/info/law/law-topic/data-protection/data-transfers-outside-eu/adequacy-protection-personal-data-non-eu-countries_en\">European Commission: Adequacy of the protection of personal data in non-EU countries</a>.</li><li>Where we use certain service providers, we may use specific contracts approved by the European Commission which give personal data the same protection it has in Europe. For further details, see <a href=\"https://ec.europa.eu/info/law/law-topic/data-protection/data-transfers-outside-eu/model-contracts-transfer-personal-data-third-countries_en\">European Commission: Model contracts for the transfer of personal data to third</a> countries.</li><li>Where we use providers based in the US, we may transfer data to them if they are part of the Privacy Shield which requires them to provide similar protection to personal data shared between the Europe and the US. For further details, see <a href=\"https://ec.europa.eu/info/law/law-topic/data-protection/data-transfers-outside-eu/eu-us-privacy-shield_en\">European Commission: EU-US Privacy Shield</a>.</li></ul></p><p>Please contact us at privacy@tieto.com, if you want further information on the specific mechanism used by us when transferring your personal data out of the EEA.</p>"},{"category":"General notice","title":"Data retention","hash":"data-retention","intro":"<p>We will only retain your personal information for as long as necessary to fulfil the purposes we collected it for, including for the purposes of satisfying any legal, accounting, or reporting requirements. </p><p>To determine the appropriate retention period for personal data, we consider the amount, nature, and sensitivity of the personal data, the potential risk of harm from unauthorised use or disclosure of your personal data, the purposes for which we process your personal data and whether we can achieve those purposes through other means, and the applicable legal requirements.</p><p>In some circumstances we may anonymise your personal information so that it can no longer be associated with you, in which case we may use such information without further notice to you. </p>","content":""},{"category":"General notice","title":"Rights of access, rectification, erasure, and restriction","hash":"rights-of-access-rectification-erasure-and-restriction","intro":"<p><strong>Under certain circumstances, by law you have the right to:</strong></p><p> <ul> <li> <strong>Request access</strong> to your personal data (commonly known as a “data subject access request”). This enables you to receive information on how we process your personal data and a copy of the personal data we hold about you. </li><li><strong>Request correction</strong> of the personal data that we hold about you. This enables you to have any incomplete or inaccurate data we hold about you corrected, though we may need to verify the accuracy of the new data you provide to us.</li><li><strong>Request erasure</strong> of your personal data. This enables you to ask us to delete or remove personal data where there is no good reason for us continuing to process it. You also have the right to ask us to delete or remove your personal data where you have successfully exercised your right to object to processing (see below), where we may have processed your information unlawfully or where we are required to erase your personal data to comply with local law. Note, however, that we may not always be able to comply with your request of erasure for specific legal reasons which will be notified to you, if applicable, at the time of your request.</li><li><strong>Object to processing</strong> of your personal data where we are relying on a legitimate interest (or those of a third party) and there is something about your particular situation which makes you want to object to processing on this ground as you feel it impacts on your fundamental rights and freedoms. You also have the right to object where we are processing your personal data for direct marketing purposes. In some cases, we may demonstrate that we have compelling legitimate grounds to process your information which override your rights and freedoms.</li><li><strong>Request restriction</strong> of processing of your personal data. This enables you to ask us to suspend the processing of your personal data in the following scenarios: (a) if you want us to establish the data’s accuracy; (b) where our use of the data is unlawful but you do not want us to erase it; (c) where you need us to hold the data even if we no longer require it as you need it to establish, exercise or defend legal claims; or (d) you have objected to our use of your data but we need to verify whether we have overriding legitimate grounds to use it.</li><li><strong>Request the transfer</strong> of your personal data to you or to a third party. We will provide to you, or a third party you have chosen, your personal data in a structured, commonly used, machine-readable format. Note that this right only applies to automated information which you initially provided consent for us to use or where we used the information to perform a contract with you.</li></ul></p><p>If you want to review, verify, correct or request erasure of your personal information, object to the processing of your personal data, or request that we transfer a copy of your personal information to another party, please contact us at privacy@tieto.com.</p><p><strong>No fee usually required</strong></p><p>You will not have to pay a fee to access your personal information (or to exercise any of the other rights). However, we may charge a reasonable fee if your request for access is clearly unfounded or excessive. Alternatively, we may refuse to comply with the request in such circumstances.</p><p><strong>What we may need from you</strong></p><p>We may need to request specific information from you to help us confirm your identity and ensure your right to access the information (or to exercise any of your other rights). This is another appropriate security measure to ensure that personal information is not disclosed to any person who has no right to receive it.</p>","content":""},{"category":"General notice","title":"Right to withdraw consent","hash":"right-to-withdraw-consent","intro":"<p>In the limited circumstances where you may have provided your consent to the collection, processing and transfer of your personal information for a specific purpose, you have the right to withdraw your consent for that specific processing at any time. To withdraw your consent, please contact us at privacy@tieto.com. Once we have received notification that you have withdrawn your consent, we will no longer process your information for the purpose or purposes you originally agreed to, unless we have another legitimate basis for doing so in law.</p>","content":""},{"category":"General notice","title":"Automated decision-making","hash":"automated-decision-making","intro":"<p>Automated decision-making takes place when an electronic system uses personal information to make a decision without human intervention. </p><p>You will not be subject to decisions that will have a significant impact on you based solely on automated decision-making, unless we have a lawful basis for doing so and we have notified you. </p><p>If we make an automated decision on the basis of any particularly sensitive personal information, we must have either your explicit written consent or it must be justified in the public interest, and we must also put in place appropriate measures to safeguard your rights.</p>","content":""},{"category":"General notice","title":"Data security","hash":"data-security","intro":"<p>Third parties will only process your personal information on our instructions and where they have agreed to treat the information confidentially and to keep it secure.</p><p>We have put in place appropriate security measures to prevent your personal information from being accidentally lost, used or accessed in an unauthorised way, altered or disclosed. In addition, we limit access to your personal information to those employees, agents, contractors and other third parties who have a business need to know. They will only process your personal information on our instructions and they are subject to a duty of confidentiality.</p><p>We have put in place procedures to deal with any suspected data security breach and will notify you and any applicable regulator of a suspected breach where we are legally required to do so.</p>","content":""},{"category":"General notice","title":"Contact Details","hash":"contact-details","intro":"<p><strong>Contact Details</strong></p><p>The contact details of Tieto Corporation:</p><p>Full name of legal entity: Tieto Oyj</p><p>Postal address: Keilalahdentie 2-4, 02150 Espoo</p><p>Telephone number: +3582072010</p><p>The contact details of other data controllers responsible for your personal data can be found here [<a href=\"https://www.tieto.com/addresses\" target=\"_blank\" rel=\"noopener noreferrer\">https://www.tieto.com/addresses</a>].</p><p><strong>Data Protection Officer</strong></p><p>We have appointed a <strong>data protection officer (DPO)</strong> who is responsible for overseeing questions in relation to this privacy notice. If you have any questions about this privacy notice, including any requests to exercise your legal rights, please contact us at privacy@tieto.com.</p><p>You have the right to make a complaint at any time to the competent supervisory authority for data protection issues. We would, however, appreciate the chance to deal with your concerns before you approach the authority so please contact us directly in the first instance.</p>","content":""},{"category":"General notice","title":"Changes and third party links","hash":"changes-and-third-party-links","intro":"<p><strong>Changes to the Privacy notice</strong></p><p>Tieto reserves the right to amend this General Privacy notice and specific privacy notices at any time, for any reason, without notice to you, other than the posting of the amended privacy notices on this website. You must review this Privacy notice from time to time for any amendments.</p><p><strong>Third party links</strong></p><p>This website may include links to third-party websites, plug-ins and applications. Clicking on those links or enabling those connections may allow third parties to collect or share data about you. We do not control these third-party websites and are not responsible for their privacy statements. When you leave our website, we encourage you to read the privacy notice of every website you visit.</p>","content":""},{"category":"Website Visitor notice","title":"Scope of this Privacy notice","hash":"scope-of-this-privacy-notice-1","intro":"<p>This privacy notice aims to give you information on how Tieto collects and processes your personal data and tells you about your privacy rights and how the law protects you, when you:</p><p> <ol> <li>visit our websites (regardless of where you visit is from), or</li><li>while visiting our website e.g. fill in forms, subscribe to our newsletters and whitepapers, request marketing to be sent to you, ask us to contact you for other reasons, take part in a competition, promotion or survey, or give us some feedback or comment our content. </li></ol></p>","content":""},{"category":"Website Visitor notice","title":"Personal data categories","hash":"personal-data-categories-2","intro":"<p>Personal data, or personal information, means any information about an individual from which that person can be identified. It does not include data where the identity has been removed (anonymous data).</p><p>We collect, use, store and transfer different kinds of personal data about you which we have grouped together as follows:</p><p> <ul> <li><strong>Identity Data</strong> such as first name, last name, username or similar identifier.</li><li><strong>Contact Data</strong> such as address, email address, telephone numbers, social media contact.</li><li><strong>Employment Data</strong> such as title, company name, department name.</li><li><strong>Technical Data</strong> such as internet protocol (IP) address, your login data, browser type and version, time zone setting and location, browser plug-in types and versions, operating system and platform and other technology on the devices you use to access this website.</li><li><strong>Profile Data</strong> such as your username and password, purchases or orders made by you, your interests, preferences, feedback and survey responses.</li><li><strong>Usage Data</strong> such as information about how you use our website, products and services.</li><li><strong>Marketing and Communications Data</strong> such as your preferences in receiving marketing from us and our third parties and your communication preferences.</li></ul></p>","content":"<p>We also collect, use and share Aggregated Data such as statistical or demographic data for any purpose. Aggregated Data may be derived from your personal data but is not considered personal data in law as this data does not directly or indirectly reveal your identity. For example, we may aggregate your Usage Data to calculate the percentage of users accessing a specific website feature. However, if we combine or connect Aggregated Data with your personal data so that it can directly or indirectly identify you, we treat the combined data as personal data which will be used in accordance with this privacy notice.</p><p>We do not generally collect any special categories of personal data about you (this includes details about your race or ethnicity, religious or philosophical beliefs, sex life, sexual orientation, political opinions, trade union membership, information about your health and genetic and biometric data). Nor do we collect any information about criminal convictions and offences. In the limited cases where we do process such data, we do this in accordance with the applicable legislation. If you choose to provide us with unsolicited sensitive personal data through our websites, you are consenting to the processing of that personal information to the extent necessary and to the extent permitted by applicable legislation.</p>"},{"category":"Website Visitor notice","title":"Collection of personal data","hash":"collection-of-personal-data-2","intro":"<p>We use different methods to collect data from and about you including through:</p><p><strong>Direct interactions.</strong> You may give us your personal data by filling in forms or by corresponding with us by post, phone, email or otherwise.</p><p><strong>Automated technologies or interactions.</strong> As you interact with our website, we may automatically collect Technical Data about your equipment, browsing actions and patterns. We collect this personal data by using cookies, server logs and other similar technologies. We may also receive Technical Data about you if you visit other websites employing our cookies. Please see our Cookie notice for further details.</p><p><strong>Third parties or publicly available sources.</strong> We may receive personal data about you from various third parties and public sources as set out below:</p><p> <ul> <li>Technical Data from analytics providers, advertising networks and search information providers.</li></ul></p><p><strong>IF YOU FAIL TO PROVIDE PERSONAL DATA</strong></p><p>Where we need to collect personal data by law, or under the terms of a contract we have with you and you fail to provide that data when requested, we may not be able to perform the contract we have or are trying to enter into with you (for example, to provide you with goods or services). In this case, we may have to cancel a product or service you have with us but we will notify you if this is the case at the time.</p>","content":""},{"category":"Website Visitor notice","title":"Purpose and legal basis of processing","hash":"purpose-and-legal-basis-of-processing","intro":"<p>We will only use your personal information when the law allows us to. We have set out below, in a table format, a description of all the ways we plan to use your personal data, and which of the legal bases we rely on to do so. We have also identified what our legitimate interests are where appropriate.</p><p>Note that we may process your personal data for more than one lawful ground depending on the specific purpose for which we are using your data. Please contact us at privacy@tieto.com if you need details about the specific legal ground we are relying on to process your personal data where more than one ground has been set out in the table below.</p><table cellspacing=\"0\" cellpadding=\"0\"> <tr> <td style=\"width:50%\"><strong>Purpose/Activity</strong></td><td><strong>Lawful basis for processing</strong></td></tr><tr> <td> To manage our relationship with you, including <ul> <li>managing and answering to your requests submitted on the website</li><li>Notifying you about changes to our terms or privacy policy</li><li>Asking you to leave a review or take a survey</li><li>Communicating with you</li></ul> </td><td> <ol style=\"list-style-type: lower-alpha;\"> <li>Performance of a contract with you</li><li>Necessary to comply with a legal obligation</li><li>Necessary for our legitimate interests (to keep our records updated and to study how customers use our products/services, to develop them and grow our business)</li></ol> </td></tr><tr> <td> To administer and protect our business and this website, including <ul> <li>troubleshooting, data analysis, testing, system maintenance, support, reporting and hosting of data</li></ul> </td><td> <ol style=\"list-style-type: lower-alpha;\"> <li>Necessary for our legitimate interests (for running our business, provision of administration and IT services, network security, to prevent fraud and in the context of a business reorganisation or group restructuring exercise) </li></ol> </td></tr><tr> <td> Marketing and marketing analytics, including <ul> <li>Delivering relevant website content and advertisements to you and measure or understand the effectiveness of the advertising we serve to you</li><li>Using data analytics to improve our website, products/services, marketing, customer relationships and experiences</li><li>Making suggestions and recommendations to you about goods or services that may be of interest to you </li></ul> </td><td> <ol style=\"list-style-type: lower-alpha;\"> <li>Necessary for our legitimate interests (to define types of customers for our products and services, to keep our website updated and relevant, to study how customers use our products/services, to develop them, to grow our business and to inform our marketing strategy)</li><li>You have given consent to the processing of your personal information for one or more specific purposes.</li></ol> </td></tr><tr> <td> Administrative reasons and legal compliance <ul> <li>complying with legal obligations</li></ul> </td><td> Necessary for our legitimate interests (for the purposes of satisfying any legal, accounting, or reporting requirements) </td></tr></table><p><strong>MARKETING COMMUNICATION AND OPTING OUT </strong></p><p>We strive to provide you with choices regarding certain personal data uses, particularly around marketing and advertising. </p><p>You can ask us or third parties to stop sending you marketing messages at any time by checking or unchecking relevant boxes to adjust your marketing preferences or by following the opt-out links on any marketing message sent to you or by contacting us at privacy@tieto.com at any time. In such cases, we will retain minimum personal data to note that you opted out in order to avoid contacting you again.</p><p>Where you opt out of receiving these marketing messages, this will not apply to personal data provided to us as a result of a product/service purchase, warranty registration, product/service experience or other transactions.</p><p><strong>PROMOTIONAL OFFERS FROM US</strong></p><p>We may use your peronal information to form a view on what we think you may want or need, or what may be of interest to you. This is how we decide which products, services and offers may be relevant for you (we call this marketing).</p><p>You will receive marketing communications from us if you have given your consent to us sending marketing communication to you, or if you (or the company you represent) have purchased goods or services from us and you have not opted out of receiving that marketing.</p><p><strong>THIRD-PARTY MARKETING</strong></p><p>We will get your express opt-in consent before we disclose your personal data with any company outside the Tieto group of companies for marketing purposes.</p><p><strong>TARGETED EMAIL</strong></p><p>We send commercial e-mails that typically include technologies (web beacons, cookies or similar) that allow us to know whether you open, read, or delete the message, and links you may click. When you click a link in a marketing e-mail you receive from us, we will also use a cookie to log what pages you view and what content you download from our websites, even if you are not registered at or signed into our site.</p><p><strong></strong></p><p>We may combine the data collected about you from publicly available sources, and from our different e-mail, website, and personal interactions with you. We combine this data to better assess your experience with Tieto and to perform the other activities described in our privacy notice.</p><p><strong>COOKIES</strong></p><p>When you visit our Website, we may employ cookies that contain information (such as a unique cookie ID) used to track your usage of our Website. This enables our systems to recognise your browser and tell us how, when and which pages in our Website are visited and by how many people, and also enables us to recognise a return visitor as a unique user. For more information about the cookies we use, please see our separate Cookie notice.</p>","content":""},{"category":"Business Relations and External stakeholders notice","title":"Scope of this Privacy notice","hash":"scope-of-this-privacy-notice-2","intro":"<p>This privacy notice aims to give you information on how Tieto collects and processes your personal data and tells you about your privacy rights and how the law protects you, when:</p><ol> <li><strong>your personal information is used for the management of our relationship with our customer, or</strong></li><li><strong>your personal information is used for the management of our relationship with our vendor or partner, or</strong></li><li><strong>your personal information is used for used for the execution of our sales opportunity, or</strong></li><li><strong>your personal information is used in connection of our offering delivery for our own business purposes, or</strong></li><li><strong>you are acting in a position where there is another legitimate business purpose for the processing of your data (external stakeholder). This can be the case, for example when you are working for press and media and your personal information is used for our external communication purposes. </strong></li></ol>","content":""},{"category":"Business Relations and External stakeholders notice","title":"Personal data categories","hash":"personal-data-categories-3","intro":"<p>Personal data, or personal information, means any information about an individual from which that person can be identified. It does not include data where the identity has been removed (anonymous data).</p><p>We collect, use, store and transfer different kinds of personal data about you which we have grouped together follows:</p><ul> <li><strong>Identity Data</strong> such as first name, last name, username or similar identifier.</li><li><strong>Contact Data</strong> such as address, email address and telephone numbers.</li><li><strong>Employment Data</strong> such as job title, company name, department name, role classification.</li><li><strong>Profile Data</strong> such as your username and password, purchases or orders made by you, your interests, preferences, feedback and survey responses.</li><li><strong>Usage Data</strong> such as information about how you use our website, products and services.</li><li><strong>Marketing and Communications Data</strong> such as your preferences in receiving marketing from us and our third parties and your communication preferences.</li><li><strong>Sales and Customer Relationship Data</strong> such as role in the company, access rights, offering details and activity.</li><li><strong>Project management and Continuous services related Data</strong> such as allocations, hour bookings and role.</li></ul><p>We also collect, use and share Aggregated Data such as statistical or demographic data for any purpose. Aggregated Data may be derived from your personal data but is not considered personal data in law as this data does not directly or indirectly reveal your identity. For example, we may aggregate your Usage Data to calculate the percentage of users accessing a specific website feature. However, if we combine or connect Aggregated Data with your personal data so that it can directly or indirectly identify you, we treat the combined data as personal data which will be used in accordance with this privacy notice.</p><p>We do not generally collect any special categories of personal data about you (this includes details about your race or ethnicity, religious or philosophical beliefs, sex life, sexual orientation, political opinions, trade union membership, information about your health and genetic and biometric data). Nor do we collect any information about criminal convictions and offences. In the limited cases where we do process such data, we do this in accordance with the applicable legislation.</p>","content":""},{"category":"Business Relations and External stakeholders notice","title":"Collection of personal data","hash":"collection-of-personal-data-3","intro":"<p>We use different methods to collect data from and about you including through:</p><p><strong>Direct interactions.</strong> You may give us your personal information by filling in forms or by corresponding with us by post, phone, email or otherwise. This information will be then included to our Customer Relationship Management (CRM) or Vendor/Partner management databases by our professionals based on their interactions with you. </p><p><strong>Submitted to us by the company we have connection with.</strong> Your personal information can be submitted to us directly by the company we are connected to for the purposes of facilitating the relationship with us.</p><p><strong>Third parties or publicly available sources.</strong> We may receive personal data about you from various third parties and public sources as set out below:</p><p> <ul> <li> <strong>Identity, Contact, and Employment Data</strong> from third parties (including data brokers or aggregators) or from publicly available sources (e.g. published articles, press releases, your public posts on social media sites if relevant for business purpose). </li></ul></p>","content":""},{"category":"Business Relations and External stakeholders notice","title":"Purpose and legal basis of processing","hash":"purpose-and-legal-basis-of-processing-2","intro":"<p>We have set out below, in a table format, a description of all the ways we plan to use your personal data, and which of the legal bases we rely on to do so. We have also identified what our legitimate interests are where appropriate.</p><p>Note that we may process your personal data for more than one lawful ground depending on the specific purpose for which we are using your data. Please contact us at privacy@tieto.com if you need details about the specific legal ground we are relying on to process your personal data where more than one ground has been set out in the table below.</p><table cellspacing=\"0\" cellpadding=\"0\"> <tr> <td style=\"width:50%;\"> <strong>Purpose/Activity</strong> </td><td> <strong>Lawful basis for processing including basis of legitimate interest</strong> </td></tr><tr> <td> Sales and Customer relationship management, including: <ul> <li>implementing, developing and maintaining business relationship with our customer</li><li>managing delivery </li><li>asking you to leave a review or take a survey</li><li>providing customer support</li><li>managing invoicing, reporting and contracts</li></ul> </td><td> Necessary for our legitimate interests (for managing our customer relationship, and for the purposes of satisfying any legal, accounting, or reporting requirements) </td></tr><tr> <td> Sales and Customer relationship management, including: <ul> <li>implementing, developing and maintaining business relationship with our customer</li><li>managing delivery</li><li>asking you to leave a review or take a survey</li><li>providing customer support</li><li>managing invoicing, reporting and contracts</li></ul> </td><td> <ol style=\"list-style-type: lower-alpha;\"> <li>Necessary for our legitimate interests (to define types of customers for our products and services, to keep our website updated and relevant, to study how customers use our products/services, to develop them, to grow our business and to inform our marketing strategy)</li><li>You have given consent to the processing of your personal information for one or more specific purposes.</li></ol> </td></tr><tr> <td> External communication purposes, including <ul> <li>Managing media contacts</li><li>Communicating relevant content externally</li></ul> </td><td> Necessary for our legitimate interests (for the purposes of managing media relations and comlying with our obligation of transparency as a publicly traded company) </td></tr><tr> <td> Vendor and partner relationship management <ul> <li>implementing, developing and maintaining business relationship</li><li>receiving customer support</li><li>managing invoicing, reporting and contracts</li></ul> </td><td> Necessary for our legitimate interests (for the purposes of managing the business relationship) </td></tr><tr> <td> Project management and continuous service delivery </td><td> Necessary for our legitimate interests (for the purposes of delivering and managing the offering) </td></tr><tr> <td> Administrative reasons and legal compliance <ul> <li>complying with legal obligations</li></ul> </td><td> Necessary for our legitimate interests (for the purposes of satisfying any legal, accounting, or reporting requirements) </td></tr></table><p><strong>MARKETING COMMUNICATION AND OPTING OUT</strong></p><p>We strive to provide you with choices regarding certain personal data uses, particularly around marketing and advertising. </p><p>You can ask us or third parties to stop sending you marketing messages at any time by checking or unchecking relevant boxes to adjust your marketing preferences or by following the opt-out links on any marketing message sent to you or by contacting us at privacy@tieto.com at any time. In such cases, we will retain minimum personal data to note that you opted out in order to avoid contacting you again.</p><p>Where you opt out of receiving these marketing messages, this will not apply to personal data provided to us as a result of a product/service purchase, warranty registration, product/service experience or other transactions.</p><p><strong>PROMOTIONAL OFFERS FROM US </strong></p><p>We may use your personal information to form a view on what we think you may want or need, or what may be of interest to you. This is how we decide which products, services and offers may be relevant for you (we call this marketing).</p><p>You will receive marketing communications from us if you have given your consent to us sending marketing communication to you, or if you (or the company you represent) have purchased goods or services from us and you have not opted out of receiving that marketing.</p><p><strong>THIRD-PARTY MARKETING</strong></p><p>We will get your express opt-in consent before we share your personal data with any company outside the Tieto group of companies for marketing purposes.</p><p><strong>TARGETED EMAIL</strong></p><p>We send commercial e-mails that typically include technologies (web beacons, cookies or similar) that allow us to know whether you open, read, or delete the message, and links you may click. When you click a link in a marketing e-mail you receive from us, we will also use a cookie to log what pages you view and what content you download from our websites, even if you are not registered at or signed into our site.</p><p><strong>COMBINING AND ANALYZING DATA</strong></p><p>We may combine the data collected about you from publicly available sources, and from our different e-mail, website, and personal interactions with you. We combine this data to better assess your experience with Tieto and to perform the other activities described in our privacy notice.</p>","content":""},{"category":"Recruitment notice","title":"Scope of this Privacy notice","hash":"scope-of-this-privacy-notice-3","intro":"<p>This privacy notice aims to give you information on how Tieto collects and processes your personal data and tells you about your privacy rights and how the law protects you, <strong>when your personal information is used for recruitement purposes.</strong></p>","content":""},{"category":"Recruitment notice","title":"Personal data categories","hash":"personal-data-categories-4","intro":"<p>Personal data, or personal information, means any information about an individual from which that person can be identified. It does not include data where the identity has been removed (anonymous data).</p><p>During recruitement process we may collect and process following personal data from you:</p><ul> <li><strong>Identity Data</strong> such as first name, last name, username or similar identifier.</li><li><strong>Contact Data</strong> such as address, email address and telephone numbers.</li><li><strong>Job Data</strong> such as your CV, previous work history and other related information</li><li><strong>Skills and Competencies Data</strong> such as your level of education, certificates, language skills, data from assesments and other related data.</li><li><strong>Admission Test Data</strong> (if conducted during the process) such as results of written and oral test.</li><li>In addition, we collect and process personal data you provide to us via interviews (including interviews conducted through electronic means).</li></ul>","content":"<p>We do not generally collect any special categories of personal data about you (this includes details about your race or ethnicity, religious or philosophical beliefs, sex life, sexual orientation, political opinions, trade union membership, information about your health and genetic and biometric data). </p><p>In limited situations we may collect background information such as information on credit checks and criminal convictions and offences. In the limited cases where we do process such data, we do this in accordance with the applicable legislation and if possible, we will inform you about such collection and ask for your consent.</p>"},{"category":"Recruitment notice","title":"Collection of personal data","hash":"collection-of-personal-data-4","intro":"<p>We collect data about you through:</p><p><strong>Direct interactions.</strong> We mainly collect you personal data from you, for example when you provide us with your CV, fill out our application form and provide us information about you in employment interview</p><p><strong>Third parties or publicly available sources.</strong> We may receive data about you from third parties such as</p><ul> <li>reference contacts you have given us</li><li>third parties such as talent acquisition or skill assesment companies</li><li>external websites if you have referred to such websites.</li></ul><p>We will only collect such data if we have informed you about about the collection and with your approval.</p><p><strong>IF YOU FAIL TO PROVIDE PERSONAL DATA</strong></p><p>You are not required to provide personal data to us, but where we need the personal data for evaluating your suitability for the open position you and you fail to provide that data when requested, we may not be able to proceed the recruitment process with you.</p>","content":""},{"category":"Recruitment notice","title":"Purpose and legal basis of processing","hash":"purpose-and-legal-basis-of-processing-3","intro":"<p>We will only use your personal information when the law allows us to. We have set out below, in a table format, a description of all the ways we plan to use your personal data, and which of the legal bases we rely on to do so. We have also identified what our legitimate interests are where appropriate.</p><p>Note that we may process your personal data for more than one lawful ground depending on the specific purpose for which we are using your data. Please contact us at privacy@tieto.com if you need details about the specific legal ground we are relying on to process your personal data where more than one ground has been set out in the table below.</p><table cellspacing=\"0\" cellpadding=\"0\"> <tr> <td style=\"width:50%\"> <strong>Purpose/Activity</strong> </td><td> <strong>Lawful basis for processing</strong> </td></tr><tr> <td> Recruitment purposes </td><td> <ol style=\"list-style-type: lower-alpha;\"> <li>Necessary for our legitimate interests (to assess your suitability for open position)</li><li>Performance of a contract with you</li><li>You have given consent to the processing of your personal information for one or more specific purposes.</li></ol> </td></tr><tr> <td> To consider you for future open positions for a specified time period within Tieto group of companies </td><td> You have given consent to the processing of your personal information for one or more specific purposes. </td></tr><tr> <td> To improve the recruitment experience and process through surveys and data analytics </td><td> <ol style=\"list-style-type: lower-alpha;\"> <li>Necessary for our legitimate interests (to improve the recruitment experience and process)</li><li>You have given consent to the processing of your personal information for one or more specific purposes.</li></ol> </td></tr><tr> <td> Administrative reasons and legal compliance, such as <ul> <li>diversity and inclusion requirements and practices</li><li>recordkeeping</li></ul> </td><td> <ol style=\"list-style-type: lower-alpha;\"> <li>Necessary for our legitimate interests (for the purposes of satisfying any legal, accounting, or reporting requirements)</li><li>Necessary to comply with a legal obligation</li></ol> </td></tr></table>","content":""},{"category":"Recruitment notice","title":"Consent withdrawal","hash":"consent-withdrawal","intro":"<p>If you have provided your consent to the collection, processing and transfer of your personal information for a specific purpose (such as for processing your data for future open vacancies), you have the right to withdraw your consent for that specific processing at any time. If you wish to withdraw your consent, please contact us at recruitment@tieto.com.</p>","content":""}]}

